"use client";

import { useEffect, useState } from "react";
import {
  Card,
  CardContent,
  CardHeader,
  CardTitle,
  CardDescription,
} from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { Progress } from "@/components/ui/progress";
import { Separator } from "@/components/ui/separator";
import {
  MapPin,
  ChevronRight,
  X,
  Users,
  LandPlot,
  Building2,
  TrendingUp,
  AlertCircle,
  CheckCircle,
  Leaf,
  Home,
  User,
  Shield,
  BarChart3,
  Loader2,
} from "lucide-react";

/* ================= TYPES ================= */

type Field = {
  id: number;
  zone_ids: string;
  field_name: string;
  location: string;
  farmer_name: string;
  total_area: number;
  cultivatable_area: number;
  ownership_type_display: string;
  subadmin_name: string;
  status?: "active" | "inactive" | "pending";
  soil_type?: string;
  irrigation_type?: string;
};

type ZoneCard = {
  id: number;
  name: string;
  location: string;
  totalArea: number;
  cultivatableArea: number;
  farmsCount: number;
  fields: Field[];
  efficiency?: number;
  status?: "active" | "moderate" | "low";
};

/* ================= COMPONENT ================= */

export function FarmOverview() {
  const [zones, setZones] = useState<ZoneCard[]>([
    { id: 1, name: "Zone 1", location: "—", totalArea: 0, cultivatableArea: 0, farmsCount: 0, fields: [], efficiency: 0, status: "low" },
    { id: 2, name: "Zone 2", location: "—", totalArea: 0, cultivatableArea: 0, farmsCount: 0, fields: [], efficiency: 0, status: "low" },
    { id: 3, name: "Zone 3", location: "—", totalArea: 0, cultivatableArea: 0, farmsCount: 0, fields: [], efficiency: 0, status: "low" },
    { id: 4, name: "Zone 4", location: "—", totalArea: 0, cultivatableArea: 0, farmsCount: 0, fields: [], efficiency: 0, status: "low" },
    { id: 5, name: "Zone 5", location: "—", totalArea: 0, cultivatableArea: 0, farmsCount: 0, fields: [], efficiency: 0, status: "low" },
    { id: 6, name: "Zone 6", location: "—", totalArea: 0, cultivatableArea: 0, farmsCount: 0, fields: [], efficiency: 0, status: "low" },
  ]);

  const [loading, setLoading] = useState(true);
  const [selectedZone, setSelectedZone] = useState<ZoneCard | null>(null);
  const [stats, setStats] = useState({
    totalFarms: 0,
    totalArea: 0,
    cultivatableArea: 0,
    averageEfficiency: 0,
  });

  /* ================= FETCH ALL FIELDS ================= */

  const fetchAllFields = async (): Promise<Field[]> => {
    const token = localStorage.getItem("authToken");
    
    try {
      const res = await fetch("https://rda.ngrok.app/api/fields/", {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      });

      if (!res.ok) throw new Error("Failed to fetch fields");
      
      const json = await res.json();
      return json.data || [];
    } catch (error) {
      console.error("Error fetching fields:", error);
      return [];
    }
  };

  /* ================= GROUP DATA ================= */

  const groupByZone = (fields: Field[]) => {
    const map: Record<string, ZoneCard> = {};

    fields.forEach((f) => {
      const zone = f.zone_ids;

      if (!map[zone]) {
        map[zone] = {
          id: Number(zone.split("-")[1]),
          name: zone,
          location: f.location,
          totalArea: 0,
          cultivatableArea: 0,
          farmsCount: 0,
          fields: [],
          efficiency: 0,
          status: "low",
        };
      }

      map[zone].fields.push(f);
      map[zone].farmsCount += 1;
      map[zone].totalArea += Number(f.total_area || 0);
      map[zone].cultivatableArea += Number(f.cultivatable_area || 0);
    });

    // Calculate efficiency and status for each zone
    Object.keys(map).forEach(zone => {
      const z = map[zone];
      if (z.totalArea > 0) {
        z.efficiency = Math.round((z.cultivatableArea / z.totalArea) * 100);
        z.status = z.efficiency >= 80 ? "active" : z.efficiency >= 60 ? "moderate" : "low";
      }
    });

    return map;
  };

  /* ================= LOAD ================= */

  useEffect(() => {
    const load = async () => {
      setLoading(true);
      
      const fields = await fetchAllFields();
      const zoneMap = groupByZone(fields);
      
      // Calculate overall statistics
      let totalFarms = 0;
      let totalArea = 0;
      let cultivatableArea = 0;
      let totalEfficiency = 0;
      let zoneCount = 0;

      const updatedZones = zones.map((z) => {
        if (zoneMap[z.name]) {
          const zoneData = zoneMap[z.name];
          totalFarms += zoneData.farmsCount;
          totalArea += zoneData.totalArea;
          cultivatableArea += zoneData.cultivatableArea;
          totalEfficiency += zoneData.efficiency || 0;
          zoneCount++;
          return { ...z, ...zoneData };
        }
        return z;
      });

      setZones(updatedZones);
      
      setStats({
        totalFarms,
        totalArea,
        cultivatableArea,
        averageEfficiency: zoneCount > 0 ? Math.round(totalEfficiency / zoneCount) : 0,
      });

      setLoading(false);
    };

    load();
  }, []);

  /* ================= STATUS INDICATOR ================= */

  const getStatusColor = (status: string) => {
    switch (status) {
      case "active": return "bg-emerald-500";
      case "moderate": return "bg-amber-500";
      case "low": return "bg-rose-500";
      default: return "bg-gray-500";
    }
  };

  const getStatusText = (status: string) => {
    switch (status) {
      case "active": return "High Efficiency";
      case "moderate": return "Moderate";
      case "low": return "Needs Attention";
      default: return "Inactive";
    }
  };

  /* ================= LOADING SKELETON ================= */

  if (loading) {
    return (
      <div className="space-y-6 animate-pulse">
        <div className="h-8 w-48 bg-gray-200 rounded-lg"></div>
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {[1, 2, 3, 4, 5, 6].map((i) => (
            <div key={i} className="bg-gray-100 rounded-xl p-6 space-y-4">
              <div className="h-6 w-32 bg-gray-200 rounded"></div>
              <div className="h-4 w-24 bg-gray-200 rounded"></div>
              <div className="h-2 w-full bg-gray-200 rounded"></div>
              <div className="h-10 w-full bg-gray-200 rounded"></div>
            </div>
          ))}
        </div>
      </div>
    );
  }

  /* ================= UI ================= */

  return (
    <div className="space-y-8">
      {/* ================= HEADER ================= */}
      <div className="flex flex-col md:flex-row justify-between items-start md:items-center gap-4">
        <div>
          <h1 className="text-3xl font-bold tracking-tight bg-gradient-to-r from-green-600 to-emerald-600 bg-clip-text text-transparent">
            Farm Zones Overview
          </h1>
          <p className="text-muted-foreground mt-2">
            Manage and monitor agricultural zones across the region
          </p>
        </div>
        
        <div className="flex items-center gap-2 px-4 py-2 bg-emerald-50 rounded-full">
          <CheckCircle className="w-5 h-5 text-emerald-600" />
          <span className="text-sm font-medium text-emerald-700">
            {stats.totalFarms} Active Farms
          </span>
        </div>
      </div>

      {/* ================= STATS OVERVIEW ================= */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
        <Card className="border-l-4 border-l-emerald-500 hover:shadow-md transition-all duration-300">
          <CardContent className="pt-6">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-muted-foreground">Total Area</p>
                <p className="text-2xl font-bold mt-2">{stats.totalArea.toLocaleString()} acres</p>
              </div>
              <LandPlot className="w-10 h-10 text-emerald-500 opacity-80" />
            </div>
          </CardContent>
        </Card>

        <Card className="border-l-4 border-l-blue-500 hover:shadow-md transition-all duration-300">
          <CardContent className="pt-6">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-muted-foreground">Cultivatable Area</p>
                <p className="text-2xl font-bold mt-2">{stats.cultivatableArea.toLocaleString()} acres</p>
              </div>
              <Leaf className="w-10 h-10 text-blue-500 opacity-80" />
            </div>
          </CardContent>
        </Card>

        <Card className="border-l-4 border-l-amber-500 hover:shadow-md transition-all duration-300">
          <CardContent className="pt-6">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-muted-foreground">Average Efficiency</p>
                <p className="text-2xl font-bold mt-2">{stats.averageEfficiency}%</p>
              </div>
              <TrendingUp className="w-10 h-10 text-amber-500 opacity-80" />
            </div>
          </CardContent>
        </Card>

        <Card className="border-l-4 border-l-purple-500 hover:shadow-md transition-all duration-300">
          <CardContent className="pt-6">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-muted-foreground">Active Zones</p>
                <p className="text-2xl font-bold mt-2">
                  {zones.filter(z => z.farmsCount > 0).length}/{zones.length}
                </p>
              </div>
              <Building2 className="w-10 h-10 text-purple-500 opacity-80" />
            </div>
          </CardContent>
        </Card>
      </div>

      {/* ================= ZONE CARDS ================= */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        {zones.map((zone) => (
          <Card 
            key={zone.id} 
            className="group hover:shadow-xl transition-all duration-300 border hover:border-emerald-200 cursor-pointer"
            onClick={() => zone.farmsCount > 0 && setSelectedZone(zone)}
          >
            <CardHeader className="pb-3">
              <div className="flex justify-between items-start">
                <div>
                  <CardTitle className="flex items-center gap-2 text-xl">
                    <div className={`w-3 h-3 rounded-full ${getStatusColor(zone.status || "low")}`} />
                    {zone.name}
                  </CardTitle>
                  <CardDescription className="flex items-center gap-1.5 mt-2">
                    <MapPin className="w-4 h-4" />
                    {zone.location || "Location not specified"}
                  </CardDescription>
                </div>
                <Badge 
                  variant="secondary" 
                  className={`px-3 py-1.5 font-medium ${
                    zone.farmsCount === 0 
                      ? "bg-gray-100 text-gray-600" 
                      : "bg-emerald-50 text-emerald-700 hover:bg-emerald-100"
                  }`}
                >
                  {zone.farmsCount} Farm{zone.farmsCount !== 1 ? "s" : ""}
                </Badge>
              </div>
            </CardHeader>

            <CardContent className="space-y-4">
              {/* Area Stats */}
              <div className="space-y-3">
                <div className="flex justify-between items-center text-sm">
                  <span className="text-muted-foreground">Total Area</span>
                  <span className="font-semibold">{zone.totalArea.toLocaleString()} acres</span>
                </div>
                
                <div className="space-y-1.5">
                  <div className="flex justify-between items-center text-sm">
                    <span className="text-muted-foreground">Cultivatable</span>
                    <span className="font-semibold">
                      {zone.cultivatableArea.toLocaleString()} acres
                    </span>
                  </div>
                  <Progress 
                    value={zone.efficiency} 
                    className="h-2 bg-gray-100"
                    indicatorClassName={`${getStatusColor(zone.status || "low")}`}
                  />
                  <div className="flex justify-between text-xs text-muted-foreground">
                    <span>Land Efficiency</span>
                    <span className="font-medium">{zone.efficiency}%</span>
                  </div>
                </div>
              </div>

              <Separator />

              {/* Status & Action */}
              <div className="flex justify-between items-center">
                <div className="flex items-center gap-2">
                  <span className={`px-2.5 py-1 rounded-full text-xs font-medium ${
                    zone.status === "active" 
                      ? "bg-emerald-50 text-emerald-700" 
                      : zone.status === "moderate"
                      ? "bg-amber-50 text-amber-700"
                      : "bg-rose-50 text-rose-700"
                  }`}>
                    {getStatusText(zone.status || "low")}
                  </span>
                </div>
                
                <Button
                  variant="ghost"
                  size="sm"
                  className="gap-1.5 group-hover:bg-emerald-50 group-hover:text-emerald-700 transition-colors"
                  disabled={zone.farmsCount === 0}
                  onClick={(e) => {
                    e.stopPropagation();
                    setSelectedZone(zone);
                  }}
                >
                  View Details
                  <ChevronRight className="ml-1 w-4 h-4 group-hover:translate-x-1 transition-transform" />
                </Button>
              </div>
            </CardContent>
          </Card>
        ))}
      </div>

      {/* ================= MODAL – ZONE DETAILS ================= */}
      {selectedZone && (
        <div className="fixed inset-0 z-50 flex items-center justify-center p-4 bg-black/50 backdrop-blur-sm animate-in fade-in duration-200">
          <div className="bg-white rounded-2xl w-full max-w-4xl max-h-[90vh] overflow-hidden shadow-2xl">
            {/* Modal Header */}
            <div className="sticky top-0 z-10 bg-white border-b p-6">
              <div className="flex justify-between items-start">
                <div>
                  <div className="flex items-center gap-3 mb-2">
                    <div className={`w-4 h-4 rounded-full ${getStatusColor(selectedZone.status || "low")}`} />
                    <h2 className="text-2xl font-bold">{selectedZone.name}</h2>
                    <Badge variant="secondary" className="ml-2 px-3 py-1">
                      {selectedZone.farmsCount} Farms
                    </Badge>
                  </div>
                  <div className="flex items-center gap-2 text-muted-foreground">
                    <MapPin className="w-4 h-4" />
                    <span>{selectedZone.location}</span>
                  </div>
                </div>
                <Button
                  size="icon"
                  variant="ghost"
                  className="rounded-full hover:bg-gray-100"
                  onClick={() => setSelectedZone(null)}
                >
                  <X className="w-5 h-5" />
                </Button>
              </div>

              {/* Zone Stats */}
              <div className="grid grid-cols-3 gap-4 mt-6">
                <div className="text-center p-3 bg-gray-50 rounded-lg">
                  <p className="text-sm text-muted-foreground">Total Area</p>
                  <p className="text-xl font-bold mt-1">{selectedZone.totalArea.toLocaleString()} acres</p>
                </div>
                <div className="text-center p-3 bg-emerald-50 rounded-lg">
                  <p className="text-sm text-muted-foreground">Cultivatable Area</p>
                  <p className="text-xl font-bold mt-1">{selectedZone.cultivatableArea.toLocaleString()} acres</p>
                </div>
                <div className="text-center p-3 bg-blue-50 rounded-lg">
                  <p className="text-sm text-muted-foreground">Land Efficiency</p>
                  <p className="text-xl font-bold mt-1">{selectedZone.efficiency}%</p>
                </div>
              </div>
            </div>

            {/* Farms List */}
            <div className="p-6 overflow-y-auto max-h-[calc(90vh-180px)]">
              <h3 className="text-lg font-semibold mb-4 flex items-center gap-2">
                <LandPlot className="w-5 h-5" />
                Farm Details
              </h3>
              
              <div className="space-y-4">
                {selectedZone.fields.map((field) => (
                  <Card key={field.id} className="hover:shadow-md transition-shadow duration-200">
                    <CardContent className="p-5">
                      <div className="flex flex-col md:flex-row md:items-center justify-between gap-4">
                        <div className="space-y-3">
                          <div className="flex items-center gap-2">
                            <Home className="w-5 h-5 text-emerald-600" />
                            <h4 className="font-semibold text-lg">{field.field_name}</h4>
                          </div>
                          
                          <div className="grid grid-cols-1 md:grid-cols-2 gap-3 text-sm">
                            <div className="flex items-center gap-2">
                              <User className="w-4 h-4 text-muted-foreground" />
                              <span className="font-medium">Farmer:</span>
                              <span className="text-muted-foreground">{field.farmer_name}</span>
                            </div>
                            <div className="flex items-center gap-2">
                              <Shield className="w-4 h-4 text-muted-foreground" />
                              <span className="font-medium">Ownership:</span>
                              <Badge variant="outline" className="text-xs">
                                {field.ownership_type_display}
                              </Badge>
                            </div>
                            <div className="flex items-center gap-2">
                              <Users className="w-4 h-4 text-muted-foreground" />
                              <span className="font-medium">Sub Admin:</span>
                              <span className="text-muted-foreground">{field.subadmin_name}</span>
                            </div>
                            <div className="flex items-center gap-2">
                              <BarChart3 className="w-4 h-4 text-muted-foreground" />
                              <span className="font-medium">Location:</span>
                              <span className="text-muted-foreground">{field.location}</span>
                            </div>
                          </div>
                        </div>
                        
                        <div className="md:text-right">
                          <div className="bg-emerald-50 inline-flex items-center gap-2 px-4 py-2 rounded-lg">
                            <LandPlot className="w-5 h-5 text-emerald-600" />
                            <div>
                              <p className="text-2xl font-bold text-emerald-700">
                                {field.total_area} acres
                              </p>
                              <p className="text-xs text-emerald-600 mt-1">
                                {field.cultivatable_area} acres cultivatable
                              </p>
                            </div>
                          </div>
                        </div>
                      </div>
                    </CardContent>
                  </Card>
                ))}
              </div>
            </div>

            {/* Modal Footer */}
            <div className="sticky bottom-0 bg-white border-t p-4">
              <div className="flex justify-end">
                <Button onClick={() => setSelectedZone(null)}>
                  Close
                </Button>
              </div>
            </div>
          </div>
        </div>
      )}

      {/* ================= EMPTY STATE ================= */}
      {zones.every(z => z.farmsCount === 0) && !loading && (
        <div className="text-center py-16">
          <div className="w-20 h-20 mx-auto mb-6 rounded-full bg-gray-100 flex items-center justify-center">
            <AlertCircle className="w-10 h-10 text-gray-400" />
          </div>
          <h3 className="text-xl font-semibold mb-2">No Farms Found</h3>
          <p className="text-muted-foreground max-w-md mx-auto">
            There are currently no farms assigned to any zones. Start by adding farms to the system.
          </p>
        </div>
      )}
    </div>
  );
}